.section {
  width: 100vw;
  height: 100vh;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--blue-color-black);
}

.logo {
  margin-bottom: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logoName {
  margin: 0;
  margin-left: 12px;
  font-family: "Helvetica-Regular", sans-serif;
  font-size: 24px;
}

.form {
  width: 250px;
  padding: 10px 17px;
  background: var(--white-color);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.title {
  text-align: center;
  margin: 0;
  margin-bottom: 20px;
  font-size: 18px;
}

.text {
  display: block;
  margin: 0;
  margin-bottom: 8px;
  font-size: 12px;
}

.textPassword {
  margin-top: 15px;
}

.textField {
  width: 100%;
  color: #007bff;
}

.submitBlock {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.accessHelp {
  color: #007bff;
  font-size: 12px;
  cursor: pointer;
}

@media (min-width: 540px) {
  .form {
    width: 380px;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
